    .bg-question{
        // z-index: 20;
        // opacity: 0.8;
        // position: absolute;
        // width: 100%;
        // background-color: rgb(45, 44, 44);
        // height: 100%;
        // border: 1px solid white;
        // // top: 0;
        // // left: 0;
        // // bottom: 0;
        // // right: 0;

        // border: 10px solid salmon;
        // display: none;
        // position: fixed;
        z-index: 8;
        // left: 0;
        // top: 0;
        width: 100%;
        height: 100%;
        // overflow: hidden;
        background-color: rgb(0, 0, 0);
