    .form{
        // border: 10px solid red;
        // position: fixed;
        // top: 50%;
        // left: 50%;
        // height: auto;
        // transform: translate(-50%,-50%);
        z-index: 23;
        border-radius: 5px;
        padding: 20px;
        // glassomorphy
        background: rgba(255, 255, 255, 0.2);
        border-radius: 5px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(5px);
        border: 1px solid rgba(255, 255, 255, 0.3);

        @include mobile{
            width: 65%;
        }
       .h3-question{
        font-size: 40px;
        color: #fff;
        @include mobile{
            font-size: 20px;
        }
       }
       .div-btn-question{
        display: flex;
        flex-direction: column;

        .input-name{
            padding: 15px;
            font-size: 20px;
            // glassomorphy
            border: none;
            background: rgba(255,255,255,0.05);
            backdrop-filter: blur(10px);
            border-top: 1px solid rgba(255,255,255,0.2);
            border-left: 1px solid rgba(255,255,255,0.2);
            box-shadow: 5px 5px 30px rgba(0,0,0,0.2);
            color: rgb(255, 255, 255);
            @include mobile{
                padding: 5px;
                font-size: 15px;
            }
            &:focus{
                outline: none;
            }
            &::placeholder{
               color: rgb(194, 187, 187);
            }
        }
        .btn-name{
            padding: 15px;
            margin-top: 15px;
            color: #fff;
            font-size: 20px;
            // glassomorphy
            background-color: rgba(255,255,255,0.1);
            border: 1px solid rgba(255,255,255,0.1);
            backdrop-filter: blur(30px);
            color: rgba(255,255,255,0.8);
            @include mobile{
                padding: 5px;
                font-size: 15px;
            }
        }
        .link-invite{
            margin-top: 10px;
            color: white;
        }
       }
    }
}
}
.p-error-question{
    font-size: 13px;
    color: red;
    margin: 2px 0;
    @include mobile{
        font-size: 12px;
    }
}
